import React, { Component } from 'react'
import './Header.scss';
import LogoutIcon from '../../images/logout.svg';
import LoginComponent from '../LandingPage/LandingPage';
 class Header extends Component {
    constructor(props) {
        super(props);
      }

    render() {
        let {showLogout=false} = this.props;
        return (
            <div className={'header-container'}>
                <div className={'box'}>
                 BBM Disciple Tracker
        <div onclick={()=>{return <LoginComponent/>}} className={'logout'}>{showLogout?<img src={LogoutIcon}/>:null}</div>
                 </div>
                <div className={'curve'}/>
            </div>
        )
    }
}

export default Header