import React, { Component } from "react";
import './BottomNavBar.scss';
class BottomNarBar extends Component {
  render() {
    return (
        <div className={'bottom-nav'}>
            <div className={'sticky-footer'}>

                <div className={'one-fourth'} id="search"><img class="sticky-search" src="http://seodesigns.com/projects/codepen/060516/search.png" />
                <p>search</p>
                </div>
        
                <div className={'one-fourth'} id="filters"><img class="sticky-filters" src="http://seodesigns.com/projects/codepen/060516/filters.png" />
                <p>filters</p>
                </div>
        
                <div className={'one-fourth'} id="map"><img class="sticky-map" src="http://seodesigns.com/projects/codepen/060516/map.png" />
                <p>map</p>
                </div>
        
                <div className={'one-fourth'} id="radius"><img class="sticky-radius" src="http://seodesigns.com/projects/codepen/060516/radius.png" />
                <p>radius</p>
                </div>
            </div>
        </div>
    );
  }
}


export default BottomNarBar