import React, { Component } from 'react'
import { Switch, Route, Redirect, withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import Header from './components/Header/Header';
import LoginComponent from './components/Login/Login';
import LandingPage from './components/LandingPage/LandingPage'
import BishopComponent from './components/Bishop/Bishop';
import AdminComponent from './components/Admin/Admin';
import DisciplerComponent from './components/Discipler/Discipler';

import { HOME, LOGIN, LANDING_PAGE, DISCIPLER, ADMIN, BISHOP } from './components/constants/pageUrls';

class routes extends Component {
    constructor(props){
        super(props);
        this.state = {
            isLoggedIn: false
        };
    }

    render() {
        let {isLoggedIn } = this.state;
        let redirectUrl = isLoggedIn ? LANDING_PAGE : LOGIN;
        return (
            <div>
                <Header showLogout={isLoggedIn}/>
                 <Switch>
                    { !isLoggedIn &&
                        <Route exact  path={LOGIN}>
                            <LoginComponent isLoggedIn={isLoggedIn}/> 
                        </Route>
                    }
                    {
                       isLoggedIn &&
                        <Route exact path={LANDING_PAGE}>
                            <LandingPage />                       
                        </Route>
                    }
                    {
                       isLoggedIn &&
                        <Route exact path={DISCIPLER}>
                            <DisciplerComponent />                       
                        </Route>
                    }
                    {
                       isLoggedIn &&
                        <Route exact path={ADMIN}>
                            <AdminComponent />                       
                        </Route>
                    }
                    {
                       isLoggedIn &&
                        <Route exact path={BISHOP}>
                            <BishopComponent />                       
                        </Route>
                    }
                    <Route exact path={HOME}>
                      <div/>
                    </Route>
                    <Redirect to={redirectUrl} />
                 </Switch>
            </div>
        )
    }
}


export default routes;